
import { defineComponent } from 'vue';
import {
  AjaxHelper,
  MatomoUrl,
} from 'CoreHome';
import { Field } from 'CorePluginsAdmin';

export default defineComponent({
  props: {
    metrics: {
      type: Object,
      required: true,
    },
    limits: {
      type: Object,
      required: true,
    },
    selectedMetric: {
      type: String,
      required: true,
    },
    selectedLimit: {
      type: Number,
      required: false,
      default: 10,
    },
  },
  data() {
    return {
      metric: this.selectedMetric,
      limit: this.selectedLimit,
    };
  },
  components: {
    Field,
  },
  methods: {
    updateSelection() {
      MatomoUrl.updateHash({
        ...MatomoUrl.hashParsed.value,
        metric: this.metric,
        filter_limit: this.limit,
      });

      this.saveParameters();

      window.location.reload();
    },
    saveParameters() {
      AjaxHelper.post(
        {
          module: 'CoreHome',
          action: 'saveViewDataTableParameters',
          report_id: 'Cohorts.getCohortsTable',
          segment: '',
        },
        {
          parameters: JSON.stringify({
            metric: this.metric,
            filter_limit: this.limit,
          }),
        },
        {
          withTokenInUrl: true,
          format: 'html',
        },
      ).catch(() => {
        // ignore
      });
    },
  },
});
